.App {
  text-align: center;
  /* padding: 0 24px; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.lambda-click-container {
  border: 1px solid white;
  border-radius: 10px;
  padding: 10px;
  cursor: pointer;
}

.lambda-click-container:hover {
  opacity: 0.6;;
}

h1, h2, h3, h4 {
  color: #3E5481;
}